.background {
  background: url("./images/stargaze.jpg") center bottom no-repeat;
  width: 100%;
  min-height: 100vh;
  background-size: cover;
  margin: 0;
  padding: 0;
}

.landing {
  /* border: 2px solid red; */
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100%;
  /* border: 2px solid black; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.intro-overlay {
  /* z-index: -1; */
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: #111111;
  opacity: .89;
}

.header {
  z-index: 5;
  text-align: center;
}

.header > h1 {
  text-align: center;
  color: white;
  font-family: 'Poppins', sans-serif;
  margin: 0;
}

.hello {
  color: yellow;
  letter-spacing: 0.5vh;
  font-size: 2.6vh;
  text-transform: uppercase;
  text-align: center;
  text-shadow: 1px 1px 2px black;
  font-family: 'Poppins', sans-serif;
  margin: 0;
  -webkit-animation: welcome 0.4s ease-in 0s ;
          animation: welcome 0.4s ease-in 0s ;
}

.second {
  font-size: 8vh; /* 5em;*/
  margin: 0;
  padding: 0;

}

.third {
  text-shadow: 1px 1px 2px black;
  font-size: 4vh;
}

.name {
  color: rgb(204, 0, 95);
  font-family: 'Pacifico', cursive;
  margin: 0;
  padding: 0;
  opacity: 0;
  -webkit-animation: name 1.8s ease-in-out;
          animation: name 1.8s ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

button {
  background-color: Transparent;
  margin-top: 2.7vh;
  outline: none;
  font-size: 1.5em;
  color: white;
  border: 2px solid;
  padding: 1vh 2vh;
  font-weight: lighter;
  -webkit-animation: spin 0.5s linear 1.2s;
          animation: spin 0.5s linear 1.2s;
  -webkit-transition: border-color 0.5s;
  -o-transition: border-color 0.5s;
  transition: border-color 0.5s;
}

button:hover {
  border-color: rgb(204, 0, 95);
}/projects

@keyframes welcome {
  0% {
    -webkit-transform: translate(-50vh);
        -ms-transform: translate(-50vh);
            transform: translate(-50vh);
    opacity: 0.1;
  }
}

@-webkit-keyframes name {
  100% {
    opacity: 1;
  }
}

@keyframes name {
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  25% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
  50% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  75% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  25% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
  50% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  75% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
}

@media only screen and (max-width: 800px) {
  .background {
    background: url("./images/small_star.jpg") center bottom no-repeat;
    width: 100%;
    min-height: 100vh;
    background-size: cover;
    margin: 0;
    padding: 0;
  }
}



