@import "https://fonts.googleapis.com/css?family=Roboto";

body {
  font-family: Roboto;
}

#about {
  -webkit-clip-path: polygon(0 0%, 100% 4%, 100% 96%, 0% 100%);
          clip-path: polygon(0 0%, 100% 4%, 100% 96%, 0% 100%);
  text-align: center;
  background: rgb(224, 224, 224);
  width: 100%;
  margin: 0;
  margin-top: 10vh;
  padding: 0;
  padding-bottom: 4vh;
  /* border: 2px solid red; */
}

hr {
  width: 25%;
  margin-bottom: auto;
}

.about-h1 {
  font-size: 2.5rem;
  color: #444649;
  /* border: 1px solid red; */
  margin: 0;
  margin-top: 4vh;
  padding: 0;
  text-transform: uppercase;
  letter-spacing: 1vw;
  display: inline-block;
}

.about-info {
  margin-top: 3vh;
  /* border: 2px solid black; */
  max-width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 3vh;
}

.about-info > div {
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  /* border: 2px solid red; */
}

i {
  background-color: transparent;
  color: white;
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
  font-size: 2.7rem !important;
}

.square {
  width: 4.5rem;
  height: 4.5rem;
  background-color: rgb(204, 0, 95);
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
  border-radius: 4px;
  z-index: -1;
  /* border: 1px solid black; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.icon-links {
  margin-top: 10vh;
}

.grid1-img {
  width: 100%;
}

.profile {
  width: 200px;
  height: 200px;
  border: 2px solid black;
  border-radius: 100%;
  background: url("./images/webface.jpg");
  background-size: cover;
}

