#projects {
  /* border: 2px solid red; */
  text-align: center;
  margin-top: 100px;
}

.h1-project {
  margin: 0;
  margin-top: 4vh;
  display: inline-block;
  text-align: center;
}

.projectGallery {
  display: grid;
  grid-template-columns: auto;
  grid-auto-rows: auto;
  width: 100%;
  /* border: 3px solid blue; */
  justify-items: center;
}

.projectGallery > div {
  position: relative;
  overflow: hidden;
  /* border: 2px solid red; */
  max-width: 400px;
}

.projectGallery img {
  max-width:100%;
  height: auto;
  -webkit-box-shadow: 0 2px 5px black;
          box-shadow: 0 2px 5px black;
  position: relative;
}

.overlay {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  background-color:black;
  z-index: 4;
  opacity: 0;
}



.textimg {
  position: absolute;
  color: red;
  top: 45%;
  left: 0;
  opacity: 0;
  z-index: 5;
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
}

.projectGallery > div:hover .textimg {
  left: 45%;
  opacity: 1;
}

.projectGallery > div:hover .overlay {
  opacity: 0.6;
}

@media only screen and (min-width: 800px) {
  .projectGallery {
    grid-template-columns: 1fr;
    width: auto;
  }
  .projectGallery > div {
    min-width: 600px;
  }
}



